import React from 'react';

export default function Post(props) {
	const styles = {
		articleInner: {
			position: "relative",
			clear: "both",
			fontFamily: "Arial, Helvetica, sans-serif",
			border: "3px solid",
			borderRadius: "15px",
			margin: "30px auto",
			textAlign: "center"
		},
		date: {
			color: "#bbb"
		}
	};
	return (
		<article style={{ margin: "45px" }}>
			<div style={styles.articleInner}>
				<h3>{props.title}</h3>
				<hr />
				<p>{props.content}</p>
				<p style={styles.date}>{props.date}</p>
			</div>
		</article>
	);
}
